}

.icon-cross::before {
  content: "\e90d";
}

.icon-chevron-up::before {
  content: "\e911";
}

.icon-chevron-left::before {
  content: "\e912";
}

.icon-chevron-right::before {
  content: "\e913";
}

/**
 * Set up a decent box model on the root element
 */
