  transition: color 0.35s cubic-bezier(0.3, 0.86, 0.36, 0.95), background 0.35s cubic-bezier(0.3, 0.86, 0.36, 0.95), opacity 0.35s cubic-bezier(0.3, 0.86, 0.36, 0.95);
