  transition: border-color 0.35s cubic-bezier(0.3, 0.86, 0.36, 0.95);
  border: 1px solid currentColor;
  background-color: transparent;
  width: 100%;
  min-width: 0;
  padding: 1.2rem 2.4rem;
  border-radius: 0;
  -webkit-appearance: none;
}
